.hospitals {
    padding: 53px 0;
    max-width: 1000px;
    margin: 0 auto;

    &.hospitals_small {

        max-width: none;
        padding: 0;
        margin: 0;

        .hospitals__item {
            display: block;
            width: auto;


            .hospitals__item__info {
                display: inline-block;
                width: auto;
                max-width: 500px;
                padding: 0px;
            }

            .hospitals__item__action {
                display: none;
            }
        }
    }

    .hospitals__item {
        min-height: 260px;
        display: block;
        padding: 30px 0;
        border-bottom: 1px solid #e2e2e2;

        @media(max-width: 1200px) {
            padding: 30px 10px;
        }

        &:last-child {
            border-bottom: 0;
        }

        .hospitals__item__cover {
            display: inline-block;
            vertical-align: top;
            border: 0;
            width: 200px;
            height: 200px;
            margin-right: 20px;

            @media(max-width: 1200px) {
                display: none;
            }

            img {
                border: 0;
                width: 200px;
                height: 200px;
            }

            .hospitals__item__prices_mobile {
                @media(min-width: 1200px) {
                    display: none;
                }

                @media(max-width: 1200px) {
                    display: block;
                }
            }
        }



        .hospitals__item__action__type-price {


            @media(min-width: 1200px) {
                display: block;
            }

            @media(max-width: 1200px) {
                display: none;
            }

            &.mobile {
                @media(min-width: 1200px) {
                    display: none;
                }

                @media(max-width: 1200px) {
                    display: block;
                }


            }
        }

        .hospitals__item__info {
            display: inline-block;
            vertical-align: top;
            width: 536px;
            margin-right: 20px;

            @media (max-width: 992px) {
                display: block;
                width: auto;
            }

            .hospitals__item__tags {
                display: block;
                margin: 0 0 20px 0;

                .hospitals__item__tag {
                    display: inline-block;
                    background: #79c47d;
                    border-radius: 3px;
                    padding: 1px 5px;
                    color: #fff;
                    font-size: 15px;
                }
            }

            .hospitals__item__name {
                color: #5ab6e7;
                text-transform: uppercase;
                font-size: 23px;
                word-wrap: break-word;

                text-decoration: none;

                &:active,
                &:focus,
                &:hover {
                    text-decoration: none;
                    color: #23527c;
                }
            }

            .hospitals__item__address {
                margin: 10px 0 20px 0;

                .hospitals__item__address__district {
                    display: block;
                }
            }

            .hospitals__item__timeToWork {
                display: block;
                padding: 0;
                list-style-type: none;

                li {
                    display: block;

                    &:first-child {
                        &:before {
                            background: transparent url("/img/icon_workweek.png") no-repeat center;
                        }
                    }

                    &:before {
                        display: inline-block;
                        vertical-align: middle;
                        content: " ";
                        background: transparent;
                        width: 27px;
                        height: 27px;
                        margin-right: 13px;
                    }

                }
            }

            .hospitals__item__phone {
                &:before {
                    display: inline-block;
                    vertical-align: middle;
                    content: " ";
                    background: transparent url("/img/phone_icon_1.png") no-repeat center;
                    width: 21px;
                    height: 21px;
                    margin-right: 17px;
                }
            }
        }

        .hospitals__item__action {
            display: inline-block;
            vertical-align: top;
            width: 215px;
            margin: 0;


            text-transform: uppercase;

            @media(max-width: 1200px) {
                display: block;
                margin: 20px 0 0 0;
                padding: 0;
            }

            .btn {
                font-size: 18px;
                padding: 10px 33px 7px 33px;
                text-transform: uppercase;
                line-height: 1.5em;
                margin: 10px 0 0 0;
            }
        }
    }
}